circle {
    /* fill:black; */
    fill-opacity: 1;
    stroke: grey;
    stroke-width: 1px;
  }


  .link {
    /* stroke-opacity: .5; */
    stroke:  #333;
  }

 .clusterhull {
    fill: steelblue;
    /* stroke: steelblue; */
    stroke-width: 32px;
    stroke-linejoin: round;
    pointer-events: none;
    opacity: .2;
  }

.annotation path  {
    /* stroke: lightskyblue; */
    stroke-width: 10px;
  }
  
  text {
    font: 20px sans-serif;
    text-anchor: left;
  }


.active {
  stroke: black;
  stroke-width: 2px;
}

.selected {
  stroke: black;
  stroke-width: 2px;
  /* stroke-dasharray: .3em;
  stroke-opacity: .7; */
}


.tooltip > p {
  width: 250px;
}

.fill-black {
    transition: all;
}

.fill-black {
  fill: #ddd;
  stroke: #bbb;
  opacity: 1;
}


.stroke-grey {
  stroke: grey;
}


text.annotation-note-title {
    font-size: 2em;
}

path.note-line {
    stroke: lightgrey;
    stroke-width: 3px;

}

.nodetooltip{
